async function startvideo() {
    console.info("I will access to webcam")
    // 003 - Access to Cam and display it on video DIV
    const stream = await navigator.mediaDevices.getUserMedia({
        video: true
    })
    video.srcObject = stream

}


onMount(() => {
    video = document.querySelector('#cam') 
        startvideo()
        

        Quagga.init({
    inputStream: {
        name: "Live",
        type: "LiveStream",
        target: document.querySelector('#cam')    // Or '#yourElement' (optional)
    },
    //locate: true,
    decoder: {
        readers: ['ean_reader'],
        /*
        debug: {
            drawBoundingBox: true,
            showFrequency: true,
            drawScanline: true,
            showPattern: true
        }
        */
    }
}, function (err) {
    if (err) {
        console.log(err);
        return
    }
    console.log("Initialization finished. Ready to start");
    Quagga.start();
    Quagga.onDetected(detectBarcode)
});

	});



async function detectBarcode(data) {
    console.log(data)
    if (data && data.codeResult && data.codeResult.code) {
        let code = data.codeResult.code
        if (code == lastfind) { return }

        products.forEach(element => {
            console.log(element.code, code)
            if (element.code === code) {
                beep();
                product = element.product.toUpperCase();
                price = element.price;
                icon = element.icon;
                lastfind = code
                //var synth = window.speechSynthesis;
                //var utterThis = new SpeechSynthesisUtterance("EVIDENZIATORE");
                //utterThis.voice = await synth.getVoices()[1]
                //synth.speak(utterThis);
            }
        });


        
    }
 console.log(data)
}

function beep() {
    var snd = new Audio("data:audio/wav;base64,");
    snd.play();
}
   
